.form__body {
  position: relative;
}

.form__body:after {
  content: "";
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  background: rgba(51, 51, 51, .9);
  transition: all .5s;
  position: absolute;
  top: 0;
  left: 0;
}

.form__body._sending:after {
  opacity: 1;
